import  {React, useState } from "react";
import { Link } from "react-router-dom";
//import "bootstrap/dist/css/bootstrap.min.css";
//import logo from './logo.svg';


import { Navbar, Container } from 'react-bootstrap';
//import BasicMenu from "./components/BasicMenu";


function Nav () {

	const [currentUser, setCurrentUser ] = useState(0);

	return (
		<div>
			<Navbar bg="light" expand="lg" variant="dark"  >
			  <Container>
			  
				  <Link to={"/"} className="navbar-brand">
					<div className="Logo_titleWrapper__1sWUV">
						<img alt="Название" className="Logo_title__2-fGf" src="https://s3.open-broker.ru/site/files/public/jwosg5i2Eo2EN3rZ0zuLNGoE/title-black.svg"/>
					</div>
				  </Link>
				  <ul className="navbar-nav bg-dark mr-auto">
					<li className="nav-item">
					  <Link to={"/home"} className="nav-link">
						Home
					  </Link>
					</li>
			
					{ currentUser && (
					  <li className="nav-item bg-dark">
						<Link to={"/user"} className="nav-link">
						  User
						</Link>
					  </li>
					)}
				  </ul>
				  
				 
					<ul className="navbar-nav bg-dark ml-auto">
					  <li className="nav-item">
						<Link to={"/login"} className="nav-link">
						  Login
						</Link>
					  </li>
					  <li className="nav-item">
						<Link to={"/register"} className="nav-link">
						  Sign Up
						</Link>
					  </li>
					  
					</ul>
				 
				  
				</Container >
			</Navbar >
			
			
		</div>
		
	)
	  
}

/*
<li className="nav-item">
						<BasicMenu/>
					  </li>
*/

export default Nav;