import React, { useState, createContext, useContext, useEffect, Component } from "react";

// Create the context 
const RouteContext = createContext(null);

export class RC extends Component { 
	constructor(props) {
		super(props);
		//this.handleClick = this.handleClick.bind(this);
		this.state = {
			authed: false,
			showModeratorBoard: false,
			showAdminBoard: false,
			currentUser: undefined,
			Click: this.handleClick
		};
	}

	handleClick() {
		console.log(22222);
	}
	

	render() {
		//console.log(this.state);
		let subvalue = this.state;
		return ( 
			<RouteContext.Provider value = {subvalue} >
				{this.props.children}
			</RouteContext.Provider>
		)
	}
}



// Finally creating the custom hook 
export const useRoutes = () => useContext(RouteContext);